import footerIcon from '../assets/imgs/bg-pattern-home-6-about-5.svg'
export function ContactUs() {
    return (
        <div className="contact-us-area">
            <section className="contact-section-container main-container ">
                <div className="contact-section flex space-between align-center">
                    <div className="contact-title">
                        <h2>Ready to get started?</h2>
                    </div>
                    <section>
                        <div className="contact-btn"><p>contact us</p></div>
                    </section>
                </div>
            </section>
            <div className="contact-icon-container"><img src={footerIcon} alt="" /></div>
        </div>
    )
}