import personIcon from '../assets/imgs/icon-person.svg'
import cogIcon from '../assets/imgs/icon-cog.svg'
import chartIcon from '../assets/imgs/icon-chart.svg'
var gInfo = [
    {
        icon: personIcon,
        title: 'Experienced Individuals',
        desc: 'Our network is made up of highly experienced professionals who are passionate about what they do.'
    },
    {
        icon: cogIcon,
        title: 'Easy to Implement',
        desc: 'Our processes have been refined over years of implementation meaning our teams always deliver.'
    },
    {
        icon: chartIcon,
        title: 'Enhanced Productivit',
        desc: 'Our customized platform with in-built analytics helps you manage your distributed teams.'
    },
]
export function SiteInfo() {
    return (
        <section className="site-info-container">
            <section className="main-container ">
                <div className="site-info flex space-between column">
                    <div className="site-info-title">
                        <h1>{`Build & manage distributed teams like no one else.`}</h1>
                    </div>
                    <div className="site-info-desc flex column">
                        {gInfo.map(cardInfo => {
                            return <div className="site-info-card flex space-between">
                                <div className="info-card-logo">
                                    <img src={cardInfo.icon} alt="" />
                                </div>
                                <div className="info-card-desc flex column">
                                    <h3>{cardInfo.title}</h3>
                                    <p>{cardInfo.desc}</p>
                                </div>

                            </div>
                        })
                        }
                    </div>
                </div>
            </section>
        </section>
    )
}