import gresh from '../assets/imgs/gresh.png'
import kadyImg from '../assets/imgs/avatar-kady.jpg'
import aiyshaImg from '../assets/imgs/avatar-aiysha.jpg'
import arthurImg from '../assets/imgs/avatar-arthur.jpg'
var gUsers = [
    {
        _id: 'u100',
        imgUrl: kadyImg,
        fullname: 'Kady Baker',
        job: 'Product Manager at Boolmark',
        desc: '“The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.”'
    },
    {
        _id: 'u101',
        imgUrl: aiyshaImg,
        fullname: 'Aiysha Reese',
        job: 'Founder of Manage',
        desc: '“We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!”'
    },
    {
        _id: 'u102',
        imgUrl: arthurImg,
        fullname: 'Arthur Clarke',
        job: 'Co-founder of MyPhys',
        desc: '“Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.”'
    },
]
export function FeedbackSection() {
    return (
        <section className="results-dev-container main-container flex column">
            <div className="results-dev flex column">
                <h1>Delivering real results for top <br />companies. Some of our <span>success stories.</span></h1>
            </div>
            <div className="res-list-container flex space-between column align-center">
                {gUsers.map(user => {
                    return <section className="users-desc-card flex column align-center">
                        <div className="bg-style"><img src={gresh} /></div>
                        <div className="desc">
                            <p>{user.desc}</p>
                        </div>
                        <div className="user-details flex column">
                            <h3>{user.fullname}</h3>
                            <h4>{user.job}</h4>
                        </div>
                        <div className="user-img">
                            <img src={user.imgUrl} alt="" />
                        </div>
                    </section>
                })}
            </div>
        </section>
    )
}